.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
}

.card p {
    padding: 5px;
    text-align: center;
    max-width: 270px;
}

.card img {
    max-height: 150px;
}

input {
    color: grey;
    border: 1px solid grey;
    border-radius: 2px;
    font-size: 1.2rem;
    width: 300px;
    height: 20px;
}